  height: 60px;
  display: grid;
  align-items: center;
  grid-template-columns: 110px auto 200px 200px 200px 200px;
  color: #FFFFFF;
  font-family: Roboto;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  border-bottom: 1px solid #414144;
}
