  display: flex;
  align-items: center;
  padding-left: 13px;
}
.row__container .button__container.level_1 {
  padding-left: 26px;
}
.row__container .button__container.level_2 {
  padding-left: 39px;
}
.row__container .button__container.level_3 {
  padding-left: 52px;
}
.row__container .data__container {
  display: flex;
  align-items: flex-start;
  padding: 0 12px;
}
.row__container .data__container p {
  cursor: pointer;
